import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Block, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';
//import '../style.css';


class Feed extends React.Component {
    
    constructor(props) {
      super(props);
  
      this.state = {
        jokes: []
      };
    }
    componentDidMount() {
        this.loadJokes();
        
    }
    loadJokes(){
        fetch('http://api.icndb.com/jokes/random/10')
        .then(response => response.json())
        .then(data => {this.setState({ jokes: data.value});
        });
        console.log(this.state.jokes);
    }
  
    render() {
        
        const {jokes} = this.state;
        return(
            <Panel id="feed">
                <PanelHeader >Example</PanelHeader>
                <Div>
                {jokes.map(joke =>
                    <Group title="Сегодня">
                        <p className="joke">{joke.joke}</p>
                    </Group>
                )}
                </Div> 
          </Panel>
            
        )
           
    }
};


export default Feed;
